@import url(https://fonts.googleapis.com/css?family=Manjari:100,400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card-img {
  display: block;
  width: 80%;
  margin: 0 auto;
  box-shadow: 2px 2px rgba(0, 0, 0, 0.1);
}

.card-controls {
  display: flex;
  justify-content: space-between;
  margin-top: 1rem;

}

@media screen and (min-width: 720px) {
  .card-img {
    -webkit-transition: 0.2s;
    transition: 0.2s;
  }
}


  
.card-info {
  display: flex;
  flex-direction: column;
  
  }

  .card-info-field {
    display: flex;
    flex-direction: row;
    align-items: center;
    border-bottom: solid 3px #7bc8ff;
    margin-bottom: 20px;

}

#hp {
  max-height: none;
  margin: 35px 0 0 0;
}

#attacks {
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;

}

#abilities-header {
  margin-top: 1em;
}

#costs {
  display: flex;

}

.logo {
  display: block;
  margin: 10px auto;
  max-height: 80px;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.card {
  box-sizing: border-box;
  width: 90%;
  max-width: 960px;
  margin: 0 auto;
  padding: 40px;
  font-family: Manjari;
  background-color: #fff;
}

.card-search-header {
  margin-bottom: 0;
}

.card-row {
  display: flex;
  flex-direction: column;
}

.card-col-1 {
  flex: 1 1;
}

.bounce {
  -webkit-animation-name: bounce;
          animation-name: bounce;
  -webkit-animation-timing-function: cubic-bezier(0.28, 0.84, 0.42, 1);
          animation-timing-function: cubic-bezier(0.28, 0.84, 0.42, 1);
}

@-webkit-keyframes bounce {
  0% { -webkit-transform: scale(1, 1) translateY(0); transform: scale(1, 1) translateY(0); }
  10% { -webkit-transform: scale(1.1, 0.9) translateY(0); transform: scale(1.1, 0.9) translateY(0); }
  30% { -webkit-transform: scale(0.9, 1.1) translateY(-100px); transform: scale(0.9, 1.1) translateY(-100px); }
  50% { -webkit-transform: scale(1.05, 0.95) translateY(0); transform: scale(1.05, 0.95) translateY(0); }
  57% { -webkit-transform: scale(1, 1) translateY(-7px); transform: scale(1, 1) translateY(-7px); }
  64% { -webkit-transform: scale(1, 1) translateY(0); transform: scale(1, 1) translateY(0); }
  100% { -webkit-transform: scale(1, 1) translateY(0); transform: scale(1, 1) translateY(0); }
}

@keyframes bounce {
  0% { -webkit-transform: scale(1, 1) translateY(0); transform: scale(1, 1) translateY(0); }
  10% { -webkit-transform: scale(1.1, 0.9) translateY(0); transform: scale(1.1, 0.9) translateY(0); }
  30% { -webkit-transform: scale(0.9, 1.1) translateY(-100px); transform: scale(0.9, 1.1) translateY(-100px); }
  50% { -webkit-transform: scale(1.05, 0.95) translateY(0); transform: scale(1.05, 0.95) translateY(0); }
  57% { -webkit-transform: scale(1, 1) translateY(-7px); transform: scale(1, 1) translateY(-7px); }
  64% { -webkit-transform: scale(1, 1) translateY(0); transform: scale(1, 1) translateY(0); }
  100% { -webkit-transform: scale(1, 1) translateY(0); transform: scale(1, 1) translateY(0); }
}

@media screen and (min-width: 720px) {
  .card-row {
    flex-direction: row;
  }
}

body {
  margin: 0;
  padding: 2rem;
  background-color: #fafafa;
}

h1 {
  font-family: Manjari;
  margin-top: 0;
  text-align: center;
  color: #37474f;
}

h2 {
  color: #37474f;
}
